@font-face{src:url(./assets/fonts/FaceYourFears.ttf) format('truetype');font-family:'FaceYourFears';font-stretch:normal;font-weight:normal;font-style:normal}@font-face{src:url(./assets/fonts/DrawingBlood.ttf) format('truetype');font-family:'DrawingBlood';font-stretch:normal;font-weight:normal;font-style:normal}html, body{-webkit-text-rendering:optimizeLegibility;-webkit-tap-highlight-color:transparent;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-rendering:optimizeLegibility;font-variant-ligatures:none;text-size-adjust:100%;font-weight:normal;font-kerning:none;font-style:normal;background-color:#000000;letter-spacing:normal;line-height:normal;user-select:none;overflow:hidden;color:#8a0707;height:100%;width:100%;padding:0;margin:0;left:0;top:0}h1, h2, h3, h4{font-family:'FaceYourFears', sans-serif;letter-spacing:normal;line-height:normal;position:relative;color:#8a0707;display:block;padding:0;margin:0}h5, h5, strong, span, p{font-family:'DrawingBlood', sans-serif;letter-spacing:0.2rem;line-height:normal;position:relative;color:#e6e6e6;display:block;padding:0;margin:0}h1{text-transform:uppercase;letter-spacing:0.5rem;font-size:5vw}h3{letter-spacing:0.3rem;font-size:3vw}body > canvas{transition:opacity 1s ease-in 1s;position:absolute;display:block;opacity:0;height:100%;width:100%;padding:0;margin:0;left:0;top:0}